import React, { useState, useEffect } from 'react';
import { triggerBase64Download } from 'react-base64-downloader';

import service from '../../services/getStringQrCode'
import { getApplication } from "../../api/applicationService";
import { showToast } from "../../api/commomService"

const QrCode = () => {

    const [phone, setPhone] = useState();
    const [message, setMessage] = useState("");
    const [strginQrcode, setStrginQrcode] = useState("");

    useEffect(async () => {

        const { channels } = await getApplication();
        const { PhoneNumber } = channels[0].configurations;
        setPhone(`55${PhoneNumber}`);

    }, [])

    const link = `https://api.whatsapp.com/send/?phone=${phone}&text=${message}&type=phone_number&app_absent=0`;

    const fnMessage = (event) => {
        const { value } = event.target;
        setMessage(value);
    }

    const fnPhone = (event) => {
        const { value } = event.target;
        setPhone(value);
    }

    const getStringQrCode = async () => {

        if (!message) {
            showMessage('Digite um texto para gerar o QrCode', 'warning', 'Notice!');
            return false;
        }

        const responseQrCode = await service(encodeURI(link));
        const { image } = responseQrCode.data

        if (phone === undefined) {
            showMessage('Erro ao gerar o QrCode!', 'danger', 'Notice!');
            return false;
        } else {
            
            setStrginQrcode(image);
            showMessage('QrCode gerado com sucesso!', 'success', 'Notice!');
        }

    }

    const showMessage = (msg, type) => {

        setTimeout(() => {
            showToast({ type: type, message: msg })
        }, 100);

    }

    return (
        <>
            <div className="container w-100">
                <div className="row">

                    <div className="col-8">
                        <label>Texto que deseja enviar: </label>
                        <input
                            name="key"
                            type="text"
                            className="bp-input-wrapper blip-input w-100"
                            placeholder="Ex: Olá! Bem vindo ao nosso canal de atendimento"
                            value={message}
                            onChange={(e) => fnMessage(e)}
                        />

                    </div>
                    <div></div>
                    <div className="col-3">
                        {
                            strginQrcode ? (
                                <>
                                    <label style={{ marginLeft: '16%' }}>Seu QrCode: </label>
                                    <img className="" src={strginQrcode}></img>
                                    <br />
                                    <button id="status-report-csv-button" style={{ marginLeft: '16%' }} onClick={() => triggerBase64Download(strginQrcode, 'meu_qrcode')} class="no-style mh3" title="Download do QrCode">
                                        <i id="status-table-csv" class="bp-fs-5 bp-lh-simple material-icons icon icon-download">
                                        </i> Download!
                                    </button>
                                </>
                            ) : ''
                        }


                    </div>
                    <br></br>
                    <hr />
                    <div className="container w-100">
                        <hr />
                        <br />
                        <br />
                        <div className="row">
                            <div className="col-12">
                                <button onClick={getStringQrCode} className="bp-btn bp-btn--bot bp-btn--small">Gerar QrCode</button>
                            </div>
                        </div>
                    </div>

                </div>
            </div>
        </>
    )
};

export default QrCode;